@import "../../../css/module/fn";
@import "../../../css/module/color";

.card {
	position: absolute;
	left: p2m(40);
	@include shadow();
	background-color: $red;
	color: #fff;
	width: p2m(670);
	margin: p2m(50) auto;
	border-radius: p2m(15);
	
	> .qrSec {
		height: p2m(525);
		text-align: center;
		
		#imgSwitcher{
			right: 0;
			position: absolute;
			width: p2m(123);
			height: p2m(123);
		}
		
		> .qrImg {
			display: inline-block;
			width: p2m(380);
			height: p2m(380);
			background-color: #fff;
			border-radius: p2m(15);
			margin: p2m(50) auto 0;
			text-align: center;
			
			>img{
				width: p2m(360);
				height: p2m(360);
				margin: p2m(10);
			}
		}
		
		> .time {
			display: block;
			height: 2em;
			line-height: 2em;
			font-size: p2m(22);
		}
	}
	
	 .rateSec {
		background-color: #fff;
		height: p2m(430);
		text-align: center;
		padding-top: p2m(30);
		
		> p {
			
			color: #fdbb0d;
			line-height: 2em;
			font: {
				size: p2m(38);
				weight: 900;
			}
			
			&.star {
				line-height: 1em;
				margin-bottom: 1em;
				> span {
					font: {
						size: p2m(70);
					}
				}
			}
		}
		> textarea {
			width: p2m(540);
			height: p2m(160);
			border: 1px solid #d3d3d3;
			color: #333;
		}
		
	}
	
	.submit{
		display: block;
		width: 100%;
		border: none;
		background-color: $red;
		color: #fff;
		font-size: p2m(30);
		height: p2m(105);
		text-transform: uppercase;
		border-bottom-right-radius: p2m(15);
		border-bottom-left-radius: p2m(15);
	}
}

